import React from 'react'
import './Checkout.css'
import CheckoutProduct from './CheckoutProduct'
import { useStateValue } from './StateProvider'
import Subtotal from './Subtotal'
function Checkout() {
    const [{basket}, dispatch] = useStateValue()
    const removeAll = () => {
        dispatch({
            type: 'REMOVE_ALL',
        })
    }

  return (
    <div className='checkout'>
        <div className='checkout_left'>
        <img className='checkout_ad' src='https://www.carvel.com/-/media/carvel/heros/simple/carvel-sundaes-hero-desktop-1400x470.jpg?v=1&amp;d=20190212T203137Z' alt=''/>

            <div>
                <h2 className='checkout_title'>
                    Your shopping Basket
                </h2>
                {basket.map(item => (
                    <CheckoutProduct
                    id={item.id}
                    title={item.title}
                    image={item.image}
                    price={item.price}
                    rating={item.rating}
                    />
                    
                ))}
            </div>
        </div>

        <div className='checkout_right'>
            <Subtotal />

            <button className='btn' onClick={removeAll}>Remove all</button>
        </div>

        
    </div>
  )
}

export default Checkout