import React from 'react'
import './Sort.css'

import {Link} from "react-router-dom"

function Sort({title, image}) {
  return (
    <div className='sort'>
        <div className='sort_info'>
            <p>{title}</p>
            <p className='sort_price'>
            </p>
        </div>

        <Link to='/carvel2'><img src={image} alt=''/></Link>
        <Link to='/carvel2'><button className='btn'>Click</button></Link>
    </div>
  )
}

export default Sort